.place {
  &__photo-container {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    border-radius: 8px;

    overflow: hidden;

    transition: 0.3s;

    &:hover {
      transform: scale(1.03);
      box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    font-weight: bold;
  }
}
